@import '../../assets/css/variable.less';

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 72px;

  .menu {  
    display: flex;
  }

  .left {
    display: flex;
    align-items: center;
    .logo {
      margin-left : 48px;
      display: flex;
      align-items: center;
      > a {
        color: @whiteFontColor;
        text-decoration: none; 
        display: flex;
      }
      .name {    
        margin: auto 12px;
        color: @whiteFontColor;
        font-size: @20FontSize;
      }
    }
  }
  .right {
    display: flex;
    margin-right: 24px;
  }
  .small-menu {
    margin-left: 24px;
  }

}
@media only screen and (min-width: 0px) {
  .header {
    .left .logo {
      margin-left : 32px;
    }
    .menu {
      display: none;
    }
    .small-menu {
      display: block;
    }
    .lang-select,.network-select {
      display: none;
    }
  }
}

//小于760 大于375px
