.menu {
  .menu-item {
    margin-top: 24px;
    cursor: pointer;
    // display: flex;
    a {
      color : #93A1B8;
      text-decoration: none;
    }
  }
  .trade-to-earn-item,.docs-item,.more-item{
    display: flex;
    align-items: center;
    .sub-item{
      // margin : auto;
      display: flex;
      align-items: center;
    }
  }
  .docs-item,.more-item{
    img{
      margin-top: 5px;
    }
  }

  .trade-to-earn-item{
    width: 104px !important;
    justify-content: space-between;
  }
  .menu-item.selected  a{
    color : #E0ECFF;
    cursor: pointer;
  }
  .sub-menu-item {
    padding: 16px;
    text-align: left
  }
  .sub-menu-item:last-child{
    margin-bottom: 24px;
  }

  .__react_component_tooltip.show {
    opacity: 1;
    width : 216px;
    padding : 0;
  }
  .__react_component_tooltip.show::after{
    content: none;
  }
  .__react_component_tooltip.show.place-right{
    margin: 0px;
  }
}
@media only screen and (min-width : 320px) {
  .menu {
    background: #0A172B;
    position: fixed;
    flex-direction: column;
    top: 0;
    left: 0;
    padding: 24px 32px;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    flex-direction: column;
    background: #0A172B;
    .menu-item {
      .beta-icon {
        // position : absolute;
        // top: -14px;
        // left: 28px;
        width: 42px;
      }
    }
    .back-icon {
      display: block;
    }
  }
  .show-side-menu {
    .menu {
      display: flex;
    }
    .footer {
      display: block;
      bottom: 20px;
      position: fixed;
    }
  }
}
@media only screen and (min-width : 1140px){
  .menu {
    display: flex;
    justify-content: space-around;
    margin-left: 14px;
    font-size: 16px;
    background: none;
    position: initial;
    flex-direction: row;
    padding: 0px;
    
    .back-icon {
      display: none;
    }
    .menu-item.long {
      width :128px
    }
    .menu-item {
      position : relative;
      width: 96px;
      margin: auto;
      display: flex;
      justify-content: center;

      .beta-icon {
        position : absolute;
        top: -14px;
        left: 28px;
      }
    }
    .menu-item:hover > a,.menu-item:hover >.sub-menu,.menu-item.selected > a,.sub-menu-item:hover > a{
      color : #E0ECFF;
      cursor: pointer;
    }
    .__react_component_tooltip.show.place-bottom{
      margin-top: 26px;
      margin-left: 30px;
    }
    .footer {
      display: none;
    }
  }
}
.team .menu .team-item,
.token .menu .token-item,
.pool .menu .pools-item,
.futures .menu .Futures-item,
.options .menu .options-item,
.airdrop .menu .airdrop-item,
.trade-to-earn .menu .promotional-event-item,
.governance .menu .governance-item,
.info .menu .stats-item,
.bridge .bridge-item
{
  
  a{
    color: #E0ECFF;
    cursor: pointer;
  }
}
.airdrop .menu .more-item,
.trade-to-earn .menu .more-item,
.governance .menu .more-item
{
  color: #E0ECFF;
}
.team .menu .docs-item{
  color: #E0ECFF;
}